<!-- 管理视频 -->

<template>
    <el-row>
        <el-col :span="24" v-for='(videoOne, outIndex) in form' :key="outIndex">
            <div v-for='(video, inIndex) in videoOne' :key="inIndex" :span="24">
                <h1 v-if="inIndex == 0" :span="4">{{ video.name }}</h1>
                <h5 v-if="inIndex == 0" :span="8">{{ video.text }}</h5>
                <el-col :span="6" v-if="inIndex != 0">
                    <el-card :body-style="{ padding: '20px' }" class="el-card">
                        <el-image :src=video.imgurl class="image"></el-image>
                        <div style="padding: 14px;">
                            <span>{{ video.collection }}</span><br>
                        </div>
                        <el-button type="primary" @click="deleteVideo(video.id)">删除</el-button>
                    </el-card>
                </el-col>
            </div>
        </el-col>
    </el-row>
</template>

<script>
import axios from 'axios';
const options = {
    inject: ['reload'],
    data() {
        return {
            form: {

            },
            lessonName:'',
        }
    },
    methods: {
        
        selectVideo() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/video/select",
                method: 'post',
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    this.form = res.data;
                });
        },
        
        deleteVideo(videoId){
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/video/deleteVideo",
                data: videoId,
                method: 'post',
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    if(res.data){
                        alert("删除成功")
                    }else(
                        alert("删除失败")
                    );
                });
        }
    },
    mounted: function () {
        this.selectVideo();
    }
}
export default options;
</script>

<style>
.image {
    width: 100%;
    display: block;
    height: 200px;
}

.el-card {
    width: 100%;
}
</style>